.wrapper {
  margin-top: 200px;
  min-width: 120px;
  position: relative;
}

.button {
  width: calc(100% - 10px);
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
  text-align: center;
  opacity: 0.7;
  font-weight: 600;
}
.button:hover {
  background-color: #eeee;
}
.button:active {
  border: 1px solid rgb(10, 10, 207);
}

.content {
  margin-top: 5px;
  width: 100%;
  max-height: 100px;
  border: 1px solid black;
  border-radius: 5px;
  position: absolute;
  overflow: overlay;
}

.content > * {
  padding: 5px 0;
  text-align: center;
  cursor: pointer;
}

.content > *:hover {
  background-color: rgba(159, 151, 151, 0.933);
}

.content::-webkit-scrollbar {
  display: none;
}

.primary {
  background-color: rgb(226, 247, 247);
}

.secondary {
  background-color: rgb(164, 210, 251);
  color: rgb(65, 104, 6);
}
